class Data extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gender: "M",
      birthdate: null,
      grade: "",
      school: "",
      number: "",
      city: "",
      touched: {
        gender: false,
        birthdate: false,
        grade: false,
        school: false,
        number: false,
        city: false,
      },
      submitted: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.successEdit = this.successEdit.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  componentDidMount() {
    // this.props.verifyEmail(user_id, timestamp, signature, this.props.history);
  }

  handleSubmit(event) {
    // first_name, last_name, date_of_birth, organization, location, state, country, profile, gender, std, contact_number
    console.log(this.state);
    this.props.editProfile(
      null,
      null,
      this.state.birthdate,
      this.state.school,
      this.state.city,
      null,
      null,
      null,
      this.state.gender,
      this.state.class,
      this.state.number,
      this.successEdit,
      this.failedEdit
    );
    this.setState({
      submitted: true,
    });
    event.preventDefault();
  }

  successEdit() {
    this.props.history.push("/profile");
  }

  handleBlur = (field) => (evt) => {
    this.setState({
      touched: { ...this.state.touched, [field]: true },
    });
  };

  handleDateChange(date) {
    console.log(date);
    this.setState({
      birthdate: date,
    });
  }

  handleInputChange(event) {
    console.log(event);
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }

  failedEdit() {}

  render() {
    console.log(this.state);
    return (
      <div className="container resetpass">
        <div className="reset-block">
          <h4 style={{ fontSize: 35 }}>
            <strong>Student's Details</strong>
          </h4>
          <hr />
          <div>
            <Form
              gender={this.state.gender}
              birthdate={this.state.birthdate}
              grade={this.state.grade}
              school={this.state.school}
              number={this.state.number}
              city={this.state.city}
              handleBlur={this.handleBlur}
              handleInputChange={this.handleInputChange}
              handleDateChange={this.handleDateChange}
              handleSubmit={this.handleSubmit}
              submitted={this.state.submitted}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Data);
